import React, { useState, useEffect } from 'react';
import './css/Footer.css';

function Footer() {
  const [animationOverd, setAnimationOverd] = useState(0);

  const footerEvent = () => {
    const promise = new Promise((resolve) => {
      setTimeout(() => {
        resolve(setAnimationOverd(1));
      },1000)
    })
    promise.then((resolve) => {
      return new Promise((resolve) => {
        setTimeout(() => {
          resolve(setAnimationOverd(2));
        },1000)
      })
    }).then((resolve) => {
      setTimeout(() => {
        setAnimationOverd(3);
      },1000)
    })
  }

  useEffect(() => {
    window.addEventListener('load',footerEvent);
    return(() => {
      window.removeEventListener('load',footerEvent);
    })
  },[])

  return(
    <>
      <div className="footer">
        <div className="footer-images">
          <img src={`${process.env.PUBLIC_URL}/img/footer-city1.png`} alt="" className={animationOverd === 1 | animationOverd === 2 | animationOverd === 3 ? "footer-image active" : "footer-image"} />
          <img src={`${process.env.PUBLIC_URL}/img/footer-city2.png`} alt="" className={animationOverd === 2 | animationOverd === 3 ? "footer-image active" : "footer-image"} />
          <img src={`${process.env.PUBLIC_URL}/img/footer-city3.png`} alt="" className={animationOverd === 3 ? "footer-image active" : "footer-image"} />
        </div>
        <div className="footer-text">
          <h1>info</h1>
        </div>
      </div>
    </>
  )
}

export default Footer;