import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import './css/HeaderNav.css';

function HeaderNav() {
  const links = ["/", "/pageA", "/pageB", "/pageC", "/pageD"];
  const images = ["package.png"];
  const texts = ["HOME", "pageA", "pageB", "pageC", "pageD"];
  const linkItems = links.map((item,index) => 
    <>
      <li key={item.toString()} className="nav-item">
        <Link to={item}>
          <div>
            <img src={`${process.env.PUBLIC_URL}/img/${images[0]}`} />
            <h1>{texts[index]}</h1>
          </div>
        </Link>
      </li>
    </>
  )
  return(
    <>
      <ul className="header-nav">
        {linkItems}
      </ul>
    </>
  )
}

export default HeaderNav;